@import "../../scss/variables";

.stop-scrolling {
	height: 100%;
	overflow: hidden;
}

.sweet-overlay {
	background-color: black;

	/* IE8 */
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
	filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";

	/* IE8 */
	background-color: $black-6;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	display: none;
	z-index: 10000;
}

.sweet-alert {
	background-color: white;
	width: 478px;
	padding: 40px;
	text-align: center;
	position: fixed;
	left: 50%;
	top: 50%;
	margin-left: -256px;
	margin-top: -200px;
	overflow: hidden;
	display: none;
	z-index: 99999;
	border-radius: 6px;

	h2 {
		color: #334151;
		font-size: 24px;
		text-align: center;
		font-weight: 500;
		text-transform: none;
		position: relative;
		padding: 20px 0 0 0;
		display: block;
		margin-bottom: 5px;
	}

	p {
		color: #a8afc7;
		font-size: 16px;
		text-align: center;
		font-weight: 300;
		position: relative;
		text-align: inherit;
		float: none;
		margin: 0;
		padding: 0 0 20px 0;
		line-height: normal;
	}

	fieldset {
		border: none;
		position: relative;
		display: none;
	}

	.sa-error-container {
		background-color: #f1f1f1;
		margin-left: -17px;
		margin-right: -17px;
		overflow: hidden;
		padding: 0 10px;
		max-height: 0;
		-webkit-transition: padding 0.15s, max-height 0.15s;
		transition: padding 0.15s, max-height 0.15s;

		&.show {
			padding: 10px 0;
			max-height: 100px;
			-webkit-transition: padding 0.2s, max-height 0.2s;
			transition: padding 0.25s, max-height 0.25s;
		}

		.icon {
			display: inline-block;
			width: 24px;
			height: 24px;
			border-radius: 50%;
			background-color: #ea7d7d;
			color: white;
			line-height: 24px;
			text-align: center;
			margin-right: 3px;
		}

		p {
			display: inline-block;
		}
	}

	.sa-input-error {
		position: absolute;
		top: 29px;
		right: 26px;
		width: 20px;
		height: 20px;
		opacity: 0;
		-webkit-transform: scale(0.5);
		transform: scale(0.5);
		-webkit-transform-origin: 50% 50%;
		transform-origin: 50% 50%;
		-webkit-transition: all 0.1s;
		transition: all 0.1s;

		&::before,
		&::after {
			content: "";
			width: 20px;
			height: 6px;
			background-color: #f06e57;
			border-radius: 3px;
			position: absolute;
			top: 50%;
			margin-top: -4px;
			left: 50%;
			margin-left: -9px;
		}

		&::before {
			-webkit-transform: rotate(-45deg);
			transform: rotate(-45deg);
		}

		&::after {
			-webkit-transform: rotate(45deg);
			transform: rotate(45deg);
		}

		&.show {
			opacity: 1;
			-webkit-transform: scale(1);
			transform: scale(1);
		}
	}

	input {
		width: 100%;
		box-sizing: border-box;
		border-radius: 3px;
		border: 1px solid #d7d7d7;
		height: 43px;
		margin-top: 10px;
		margin-bottom: 17px;
		font-size: 18px;
		box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.06);
		padding: 0 12px;
		display: none;
		-webkit-transition: all 0.3s;
		transition: all 0.3s;

		&:focus {
			outline: none;
			box-shadow: 0px 0px 3px #c4e6f5;
			border: 1px solid #b4dbed;

			&::-moz-placeholder,
			&:-ms-input-placeholder,
			&::-webkit-input-placeholder {
				transition: opacity 0.3s 0.03s ease;
				opacity: 0.5;
			}
		}

		&::-moz-placeholder,
		&:-ms-input-placeholder,
		&::-webkit-input-placeholder {
			color: #bdbdbd;
		}
	}

	&.show-input input {
		display: block;
	}

	.sa-confirm-button-container {
		display: inline-block;
		position: relative;
	}

	.la-ball-fall {
		position: absolute;
		left: 50%;
		top: 50%;
		margin-left: -27px;
		margin-top: 4px;
		opacity: 0;
		visibility: hidden;
	}

	button {
		background-color: $primary;
		border-width: 0;
		line-height: 1.538;
		padding: 9px 20px;
		border-radius: 4px;
		transition: none;
		min-height: 38px;
		color: $white;
		display: inline-block;
		font-weight: 400;
		text-align: center;
		vertical-align: middle;
		user-select: none;
		background-color: transparent;
		border: 1px solid transparent;
		padding: 0.375rem 0.75rem;
		font-size: 0.875rem;
		line-height: 1.5;
		border-radius: 4px;
		transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
			box-shadow 0.15s ease-in-out;

		&:focus {
			outline: none;
			box-shadow: 0 0 2px rgba(128, 179, 235, 0.5), inset 0 0 0 1px $black-05;
		}

		&:hover,
		&:active {
			background-color: #0061da;
		}

		&.cancel {
			background-color: #c1c1c1;

			&:hover {
				background-color: #b9b9b9;
			}

			&:active {
				background-color: #a8a8a8;
			}

			&:focus {
				box-shadow: rgba(197, 205, 211, 0.8) 0px 0px 2px, rgba(0, 0, 0, 0.0470588) 0px 0px 0px 1px inset !important;
			}
		}

		&[disabled] {
			opacity: 0.6;
			cursor: default;
		}

		&.confirm[disabled] {
			color: transparent;

			~ .la-ball-fall {
				opacity: 1;
				visibility: visible;
				transition-delay: 0s;
			}
		}

		&::-moz-focus-inner {
			border: 0;
		}
	}

	&[data-has-cancel-button="false"] button {
		box-shadow: none !important;
	}

	&[data-has-confirm-button="false"][data-has-cancel-button="false"] {
		padding-bottom: 40px;
	}

	.sa-icon {
		width: 80px;
		height: 80px;
		border: 4px solid gray;
		-webkit-border-radius: 40px;
		border-radius: 40px;
		border-radius: 50%;
		margin: 0px auto;
		padding: 0;
		position: relative;
		box-sizing: content-box;

		&.sa-error {
			border-color: #f16d75;

			.sa-x-mark {
				position: relative;
				display: block;
			}

			.sa-line {
				position: absolute;
				height: 5px;
				width: 47px;
				background-color: #f16d75;
				display: block;
				top: 37px;
				border-radius: 2px;

				&.sa-left {
					-webkit-transform: rotate(45deg);
					transform: rotate(45deg);
					left: 17px;
				}

				&.sa-right {
					-webkit-transform: rotate(-45deg);
					transform: rotate(-45deg);
					right: 16px;
				}
			}
		}

		&.sa-warning {
			border-color: #f3ca56;

			.sa-body {
				position: absolute;
				width: 5px;
				height: 47px;
				left: 50%;
				top: 10px;
				-webkit-border-radius: 2px;
				border-radius: 2px;
				margin-left: -2px;
				background-color: #f3ca56;
			}

			.sa-dot {
				position: absolute;
				width: 7px;
				height: 7px;
				-webkit-border-radius: 50%;
				border-radius: 50%;
				margin-left: -3px;
				left: 50%;
				bottom: 10px;
				background-color: #f3ca56;
			}
		}

		&.sa-info {
			border-color: #f3ca56;

			&::before {
				content: "";
				position: absolute;
				width: 5px;
				height: 29px;
				left: 50%;
				bottom: 17px;
				border-radius: 2px;
				margin-left: -2px;
				background-color: #f3ca56;
			}

			&::after {
				content: "";
				position: absolute;
				width: 7px;
				height: 7px;
				border-radius: 50%;
				margin-left: -3px;
				top: 19px;
				background-color: #f3ca56;
			}
		}

		&.sa-success {
			border-color: #29ccbb;

			&::before,
			&::after {
				content: "";
				-webkit-border-radius: 40px;
				border-radius: 40px;
				border-radius: 50%;
				position: absolute;
				width: 60px;
				height: 120px;
				-webkit-transform: rotate(45deg);
				transform: rotate(45deg);
			}

			&::before {
				-webkit-border-radius: 120px 0 0 120px;
				border-radius: 120px 0 0 120px;
				top: -7px;
				left: -33px;
				-webkit-transform: rotate(-45deg);
				transform: rotate(-45deg);
				-webkit-transform-origin: 60px 60px;
				transform-origin: 60px 60px;
			}

			&::after {
				-webkit-border-radius: 0 120px 120px 0;
				border-radius: 0 120px 120px 0;
				top: -11px;
				left: 30px;
				-webkit-transform: rotate(-45deg);
				transform: rotate(-45deg);
				-webkit-transform-origin: 0px 60px;
				transform-origin: 0px 60px;
			}

			.sa-placeholder {
				width: 80px;
				height: 80px;
				border: 4px solid rgba(165, 220, 134, 0.2);
				-webkit-border-radius: 40px;
				border-radius: 40px;
				border-radius: 50%;
				box-sizing: content-box;
				position: absolute;
				left: -4px;
				top: -4px;
				z-index: 2;
			}

			.sa-fix {
				width: 5px;
				height: 90px;
				position: absolute;
				left: 28px;
				top: 8px;
				z-index: 1;
				-webkit-transform: rotate(-45deg);
				transform: rotate(-45deg);
			}

			.sa-line {
				height: 5px;
				background-color: #29ccbb;
				display: block;
				border-radius: 2px;
				position: absolute;
				z-index: 2;

				&.sa-tip {
					width: 25px;
					left: 14px;
					top: 46px;
					-webkit-transform: rotate(45deg);
					transform: rotate(45deg);
				}

				&.sa-long {
					width: 47px;
					right: 8px;
					top: 38px;
					-webkit-transform: rotate(-45deg);
					transform: rotate(-45deg);
				}
			}
		}

		&.sa-custom {
			background-size: contain;
			border-radius: 0;
			border: none;
			background-position: center center;
			background-repeat: no-repeat;
		}
	}
}

@media all and (max-width: 540px) {
	.sweet-alert {
		width: auto;
		margin-left: 0;
		margin-right: 0;
		left: 15px;
		right: 15px;
	}
}

/*
   * Animations
   */

@-webkit-keyframes showSweetAlert {
	0% {
		transform: scale(0.7);
		-webkit-transform: scale(0.7);
	}

	45% {
		transform: scale(1.05);
		-webkit-transform: scale(1.05);
	}

	80% {
		transform: scale(0.95);
		-webkit-transform: scale(0.95);
	}

	100% {
		transform: scale(1);
		-webkit-transform: scale(1);
	}
}

@keyframes showSweetAlert {
	0% {
		transform: scale(0.7);
		-webkit-transform: scale(0.7);
	}

	45% {
		transform: scale(1.05);
		-webkit-transform: scale(1.05);
	}

	80% {
		transform: scale(0.95);
		-webkit-transform: scale(0.95);
	}

	100% {
		transform: scale(1);
		-webkit-transform: scale(1);
	}
}

@-webkit-keyframes hideSweetAlert {
	0% {
		transform: scale(1);
		-webkit-transform: scale(1);
	}

	100% {
		transform: scale(0.5);
		-webkit-transform: scale(0.5);
	}
}

@keyframes hideSweetAlert {
	0% {
		transform: scale(1);
		-webkit-transform: scale(1);
	}

	100% {
		transform: scale(0.5);
		-webkit-transform: scale(0.5);
	}
}

@-webkit-keyframes slideFromTop {
	0% {
		top: 0%;
	}

	100% {
		top: 50%;
	}
}

@keyframes slideFromTop {
	0% {
		top: 0%;
	}

	100% {
		top: 50%;
	}
}

@-webkit-keyframes slideToTop {
	0% {
		top: 50%;
	}

	100% {
		top: 0%;
	}
}

@keyframes slideToTop {
	0% {
		top: 50%;
	}

	100% {
		top: 0%;
	}
}

@-webkit-keyframes slideFromBottom {
	0% {
		top: 70%;
	}

	100% {
		top: 50%;
	}
}

@keyframes slideFromBottom {
	0% {
		top: 70%;
	}

	100% {
		top: 50%;
	}
}

@-webkit-keyframes slideToBottom {
	0% {
		top: 50%;
	}

	100% {
		top: 70%;
	}
}

@keyframes slideToBottom {
	0% {
		top: 50%;
	}

	100% {
		top: 70%;
	}
}

.showSweetAlert {
	&[data-animation="pop"] {
		-webkit-animation: showSweetAlert 0.3s;
		animation: showSweetAlert 0.3s;
	}

	&[data-animation="none"] {
		-webkit-animation: none;
		animation: none;
	}

	&[data-animation="slide-from-top"] {
		-webkit-animation: slideFromTop 0.3s;
		animation: slideFromTop 0.3s;
	}

	&[data-animation="slide-from-bottom"] {
		-webkit-animation: slideFromBottom 0.3s;
		animation: slideFromBottom 0.3s;
	}
}

.hideSweetAlert {
	&[data-animation="pop"] {
		-webkit-animation: hideSweetAlert 0.2s;
		animation: hideSweetAlert 0.2s;
	}

	&[data-animation="none"] {
		-webkit-animation: none;
		animation: none;
	}

	&[data-animation="slide-from-top"] {
		-webkit-animation: slideToTop 0.4s;
		animation: slideToTop 0.4s;
	}

	&[data-animation="slide-from-bottom"] {
		-webkit-animation: slideToBottom 0.3s;
		animation: slideToBottom 0.3s;
	}
}

